.hamburger {
    display: none;
}

@media screen and (max-width: 428px) {
    .hamburger {
        display: flex;
        align-items: center;
        cursor: pointer;

        & p {
            font-size: 18px;
            margin-right: 10px;
        }
    }

    & .open {
        height: 2px;
        width: 20px;
        background-color: #111827;
        position: relative;

        &::before {
            content: '';
            height: 2px;
            width: 20px;
            background-color: #111827;
            position: absolute;
            top: -6px;
            left: 0;
        }

        &::after {
            content: '';
            height: 2px;
            width: 20px;
            background-color: #111827;
            position: absolute;
            bottom: -6px;
            left: 0;
        }
    }

    .close {
        height: 2px;
        width: 20px;
        background-color: #bc0606;
        position: relative;
        transform: rotate(45deg);

        &::before {
            content: '';
            height: 2px;
            width: 20px;
            background-color: #bc0606;
            position: absolute;
            bottom: 0;
            left: 0;
            transform: rotate(-90deg);
        }
    }
    
    .hideMenu {
        display: none;
    }

    .openMenu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 58px;
        background-color: #000000cf;
        height: 100vh;

        & li a {
            font-size: 28px;
            font-weight: 700;
            letter-spacing: 0.01em;
            line-height: 55px;
            color: #ffffff;
            cursor: pointer;
            transition: all 0.3s ease;

            &:hover {
                font-size: 30px;
                color: #FA4D16;
            }
        }

    }
}