header {
    height: 100vh;
    background-image: url(../../img/dog-bg-desktop.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

h1 {
    padding-top: 170px;
}

button {
    margin-top: 55px;
    background-color: #FA4D16;
    padding: 20px 50px;
    font-size: 26px;
    font-weight: 500;
    color: #ffffff;
    border-radius: 30px;
    border: none;
}

@media screen and (max-width: 428px) {
    header {
        background-image: url(../../img/dog-bg-mobile.jpeg);
        text-align: center;
    }
    h1 {
        padding-top: 50px;
    }
    button {
        margin-top: 25px;
    }
}